.skills {
    animation: one 2500ms 1 ease-in;
    position: absolute;
    bottom:15%;
    right:10%;
    background-color: #124861b0;
    color:#61dafb;
    border: solid 1px #61dafb;
    padding:0 0.5em;
    h2 {
        text-transform: uppercase;
        font-weight: 100;
        
        font-size:35px;
    }
    .wrapper {
        padding: 0.5em;
        display: flex;
        flex-direction: row;
        .one {
            display: flex;
            justify-content: center;
            font-size:10px;
        }
        .two {
            text-align:left;
            ul {
                h6 {
                    justify-content: flex-start;
                    text-transform: uppercase;
                    font-weight: 100;
                    padding-top:10px;
                    font-size:20px;
                    padding-bottom:10px;
                    margin:0;
                    &:nth-of-type(1) {
                        padding-top:0;
                    }
                }
                font-size:15px;
                list-style-type: none;
                li {
                    display: flex;
                    justify-content: flex-start;
                    ul {
                        li {
                            display:flex;
                            justify-content: flex-start;
                        }
                    }
                    
                }
            }
        }
    }
}
@keyframes one {
    0%{
      transform: scale(0%);
    }
    10% {
      transform: scaleX(10%);
      transform: scaleY(5%);
    }
    15% {
      transform: scaleX(20%);
      transform: scaleY(10%);
    }
    20% {
      transform: scaleX(40%);
      transform: scaleY(15%);
    }
    25% {
      transform: scaleX(60%);
      transform: scaleY(35%);
    }
    30% {
      transform: scaleX(80%);
      transform: scaleY(50%);
    }
    35% {
      transform: scaleY(40%);
      transform: scaleX(100%);
    }
    70% {
      transform: scaleY(100%);
    }
  }